.wrapper{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    transition: all 0.3s linear;
    .admin-title{
        margin-top: 15px;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 0.5em;
        p{
            font-weight: bolder;
            font-size: 20px;
            font-family: 'Dhurjati', sans-serif;
        }
    }
    .img-logo{
        width: 60px;
        height: 60px;
        border-radius: 50%;
        background: none;
        
    }
    .wrapper-child{
        margin-top: 20px;
        width: 100%;
        height: 100%;
        
    }
}